<template>
<div class="hi">
	<ul class="timeline" id="timelineID">
    <li class = "li" v-for="(key, entry,  index) in values" v-bind:class="{ complete: key }">
      <div class="timestamp">
        <span class="author">{{ entry }}</span>
      </div>
       <div class="status">      
        <h4 v-if="index === 0"> {{ returnVehicle }} </h4>
        <h4 v-else-if="index === 1"> {{ returnTrip }} </h4>
        <h4 v-else-if="index === 2"> {{ returnGas }} </h4>
        <h4 v-else> {{ last[index] }} </h4>
      </div>
    </li>
	</ul>     
 </div> 
</template>

<script>
export default {
  data () {
    return {
      values: {
        'Car': false,
        'Location': false,
        'Gas': false,
        'Trip': false
      },
      last: ['1', 'Destination', '3', 'Split']
    }
  },
  methods: {
    updateStyle: function (i) {
      if (this.bool[i] === true) {
        return true
      } else {
        return false
      }
    }
  },
  computed: {
    returnVehicle () {
      var x = this.$store.getters.getCar
      if (x != null) {
        var s = ''
        s += x['car_make']
        s += ' '
        s += x['car_model']
        this.$set(this.values, 'Car', true)

        return s
      }
      return 'Your Car'
    },
    returnTrip () {
      var x = this.$store.getters.getEndDest
      if (x != null) {
        this.$set(this.values, 'Location', true)
        return x.split(',')[0]
      }
      return 'Destination'
    },
    returnGas () {
      var x = this.$store.getters.getGasPrice
      if (x != null) {
        this.$set(this.values, 'Gas', true)
        return ('$' + x)
      }
      return '$'
    }
  }
}
</script>

<style lang='sass'>
.timeline
  list-style-type: none
  display: flex
  align-items: center
  justify-content: center
  padding-left: 0px;
.li
  transition: all 200ms ease-in
  width: 18%

.timestamp
  margin-bottom: 20px
  padding: 0px 40px
  display: flex
  flex-direction: column
  align-items: center
  font-weight: 100
.status
  display: flex
  justify-content: center
  border-top: 2px solid #D6DCE0
  position: relative
  transition: all 200ms ease-in  
  h4
    font-weight: 600
  &:before
    content: ''
    width: 25px
    height: 25px
    background-color: white
    border-radius: 25px
    border: 1px solid #ddd
    position: absolute
    top: -15px
    left: 42%
    transition: all 200ms ease-in 
.li.complete
  .status
    border-top: 2px solid #66DC71
    &:before
      background-color: #66DC71
      border: none
      transition: all 200ms ease-in 
    h4
      color: #66DC71


/*@media (min-device-width: 320px) and (max-device-width: 700px)
  .timeline
    list-style-type: none
    display: block
  .li
    transition: all 200ms ease-in
    display: flex
    width: inherit
  .timestamp
    width: 100px
  .status
    &:before
      left: -8%
      top: 30%
      transition: all 200ms ease-in */

</style>


